import React, { useState } from 'react';
import ApolloClient, { gql } from 'apollo-boost';
import { ApolloProvider, Query } from 'react-apollo';

import './App.css';

const client = new ApolloClient({
  uri: '/graphql',
});

const getDepartmentsQuery = gql`
  query DepartmentsQuery {
    departments {
      department_number
    }
  }
`;

const departmentQuery = gql`
  query DepartmentQuery($department_number: ID!) {
    department(department_number: $department_number) {
      day
      hosp
      rea
      rad
      dc
    }
  }
`;

function App() {
  const [departmentNumber, setDepartmentNumber] = useState('');
  return (
    <ApolloProvider client={client}>
      <div className='App'>
        <h1>COVID France API</h1>
      </div>
      <Query query={getDepartmentsQuery}>
        {({ loading, error, data }) => {
          if (loading) return <p>loading...</p>;
          if (error) console.log(error);
          return (
            <select
              value={departmentNumber}
              onChange={(e) => setDepartmentNumber(e.target.value)}
            >
              {data &&
                data.departments.map((department) => (
                  <option>{department.department_number}</option>
                ))}
            </select>
          );
        }}
      </Query>
      <div>
        {departmentNumber ? (
          <Query
            query={departmentQuery}
            variables={{ department_number: departmentNumber }}
          >
            {({ loading, error, data }) => {
              if (loading) return <p>loading...</p>;
              if (error) console.log(error);
              return (
                <ul>
                  {data &&
                    data.department.map((entry) => (
                      <li>
                        {entry.day} - hosp: {entry.hosp} - rea: {entry.rea}
                      </li>
                    ))}
                </ul>
              );
            }}
          </Query>
        ) : null}
      </div>
    </ApolloProvider>
  );
}

export default App;
